.dev-receipt-container {
    font-family: 'Courier New', Courier, monospace;
    margin: 0;
}

.dev-receipt {
    width: 300px;
    padding: 20px;
    background-color: white;
    border: 1px solid #f5f5f5;
}

.receipt-header, .receipt-footer {
    text-align: center;
}

.receipt-header {
    border-bottom: 1px dashed #000;
    margin-bottom: 10px;
    padding-bottom: 10px;
}

.receipt-footer {
    border-top: 1px dashed #000;
    margin-top: 10px;
    padding-top: 10px;
}

.receipt-body {
    margin-bottom: 10px;
}

.receipt-body .item, .receipt-footer .item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.receipt-body .item:last-child {
    margin-bottom: 0;
}

.receipt-footer p {
    margin-top: 10px;
    font-size: 0.9em;
    color: #555;
}
